<template>
    <div class="app-shopcart">
        <header id="heade">
            <nav>
                <a id="cation" @click="back" class="mui-icon mui-icon-back mui-icon-left-nav mui-pull-left"></a>
                <a href="">
                    <span  id="logo">购物车</span>
                </a>
                <a href="" class="navs mui-icon mui-icon-bars mui-icon-right-nav mui-pull-right">
                    
                </a>
            </nav>
        </header>
            <!-- 添加card.html -->
            <div class="mui-card">
				<div class="mui-card-header">
                    <!-- 全选 -->
                    <input type="checkbox" @click="selectAll">
                </div>
				<div class="mui-card-content">
					<div class="mui-card-content-inner">
						<!-- 商品信息开始 -->	    			                  
						<div class="mui-media-body" v-for="(item,i) of list" :key="i" style="height:100px">
                            <input type="checkbox" id="checkbox" :data-id="item.id" :checked="item.cb" :data-i="i" @click="modifyItem">
                            <img id="roses" :src="item.img_url">
                            <div class='mui-ellipsis'>
                                <div class="title">{{item.title}}</div>
                            </div>
                            <div class='mui-ellipsis1'>
                                <div class="price">￥{{item.price}}</div>
                                <button @click="handleReduce(i)" id="btn1">-</button>
                                <span class="price1">{{item.count}}</span>
                                <button @click="handleAdd(i)" id="btn2">+</button>
                                <input type="button" value="x" id="rem" :data-id="item.id" @click="removeItem" :data-idx="i">
                            </div>
					    </div>
                        <!-- 商品信息结束 -->
				    </div>
			    </div>
                <!-- 页脚 -->
				<div class="mui-card-footer">
                    <input type="button" @click="delItem" value="删除选中的商品">    
                </div>
			</div>
            <nav class="mui-bar mui-bar-tab">
                <a class="mui-tab-item mui-active" href="#">
                    <span class="mui-tab-label">总价:￥<p style="color:#ff894c">{{total}}</p></span>
                </a>
                <a class="mui-tab-item" href="#" style="background:#ff894c">
                    <span class="mui-tab-label">去结算</span>
                </a>
            </nav>
    </div>    
</template>
<script>
import {Toast} from "mint-ui"
export default {
    data(){
        return {
            list:[],//购物车列表
            alicb:false,//全选状态  默认未选中
            total:0,
            count:{},
        }
    },
    created(){
        this.loadMore();
    },
    methods:{
        back(){
            this.$router.go("-1");
        },
        // 减
        handleReduce(i){
            if(this.list[i].count==1)return;
            this.list[i].count--;
        },
        // 加
        handleAdd(i) {
            this.list[i].count++;
        },
        //全选按钮
        selectAll(e){
            //console.log(123);
            //1.获取全选按钮选中状态
            var rs=e.target.checked;
            //2.遍历数组中所有的元素
            for(var item of this.list){
                item.cb=rs;
            }
            //3.item.cb=全选状态
            //4.修改一下全选的数据状态
            this.allcb=rs;
        },
        modifyItem(e){
            //1.获取当前元素选中状态 true false
            var rs=e  .target.checked;
            //console.log(rs);
            //2.当前元素在数组中下标
            var i=e.target.dataset.i;
            //console.log(i);
            //3.将状态赋值当前数组下标中对象cb的值
            this.list[i].cb=rs;
            //console.log(this.list);
            //判断全选复选框状态  true false
            //1.获取数组个数
            var size1=this.list.length;
            //2.计算数组中cb true元素个数
            var count=0;
            for(var item of this.list){
                if(item.cb){
                    count++;
                }
            }
            //console.log(size1+":"+count);
            //3.数组个数==cb true个数 个数累加和
            if(count==size1){
                this.allcb=false;
            }else{
                this.allcb=true;
            }
            //4.将allcb=true
            //5.否则allcb=false
        },
        //删除选中多个商品
        delItem(){
            //console.log("删除选中商品...");
            //[{cb:true,id:1},{cb:true,id:3}]
            //"1,3"
            //1:创建空字符
            var html="";
            //2:遍历数据中元素
            for(var item of this.list){
                //3.判断cb==true
                if(item.cb){
                    //4:保存id拼接字符串
                    html+=item.id+",";
                }
            }
            if(html.length==0){
                Toast("请选择您要删除的商品");
                return;
            }
            html=html.substring(0,html.length-1);
            var url="http://127.0.0.1:3000/removeMItem?ids="+html;
            this.axios.get(url).then(result=>{
                if(result.data.code==1){
                    Toast("删除成功");
                    this.loadMore();
                }else{
                    Toast("删除失败");
                }
            })
        },
        //删除购物车内容
        removeItem(e){
            var id=e.target.dataset.id;
            var idx=e.target.dataset.idx;
            var url="http://127.0.0.1:3000/removeItem";
            var postData=this.qs.stringify({
                id:id
            });
            this.axios.post(url,postData).then(result=>{
                if(result.data.code==1){
                    this.list.splice(idx,1);
                    Toast("删除成功");
                }else{
                    Toast("删除失败");
                }
            })
        },
        //加载当前用户购物车列表
        loadMore(){
            var url="http://127.0.0.1:3000/getShopCart";
            this.axios.get(url).then(result=>{
                var rows=result.data.data;
                for(var item of rows){
                    item.cb=false;
                }
                this.list=rows;
                //console.log(this.list);
            });    
        }
    },
}
</script>
<style>
#checkbox{
    position: relative;
    top: -30px;
}
.price1{
    display: inline-block;
    width: 20px;
    height: 30px;
    text-align: center;
}
.mui-ellipsis{
    position: relative;
    top:-77px;
    left:118px;
}
.mui-ellipsis div{
    width: 178px;
    height: 20px;
    overflow: hidden;
}
.mui-ellipsis input:last-child{
    width: 30px;
    height: 30px;
    margin-left:16px;
}
.num{
    width: 30px;
}
.mui-ellipsis button{
    width: 30px;
    height: 30px;
    margin-left: 0px;
}
.price{
    position: relative;
    top:-71px;
    left: 110px;
    color: orangered;
    font-size: 16px;
}
#roses{
    width: 60px;
    height: 60px;
}
#btn1,.price1,#btn2{
    position: relative;
    top:-100px;
    left: 205px;
}
#rem{
    border: none;
    margin-left: 300px;
    margin-top: -130px;
}
#roses{
    width: 80px;
    height: 80px;
    margin-left: 20px;
}

</style>



