<template>
    <div class="app-my">
        <header id="heades">
            <nav>
                <a id="cations" @click="back" class="mui-icon mui-icon-back mui-icon-left-nav mui-pull-left"></a>
                <a href="">
                    <span id="log">个人中心</span>
                </a>
                <a id="cation2" @click="tuichu" class=" mui-icon-right-nav mui-pull-right">退出</a>
            </nav>
        </header>
        <div class="wel">
            <span>Hi! 欢迎来到花礼网</span>
            <div><a href="#" @click="Login">{{u}}</a></div>
        </div>
        <div class="mui-card" style="margin-top:-50px">
            <div class="my">
                <span>我的订单</span>
                <span>全部订单></span>
            </div>
            <ul class="mui-table-view mui-grid-view mui-grid-6">
                <li class="mui-table-view-cell mui-media mui-col-xs-4 mui-col-sm-3" v-for="(item,i) of list1" :key="i">
                    <a style="width:130px;height:72px;" href="#">
                        <img :src="item.img_url" style="width:40px;height:32px;">
                        <div class="mui-media-body">{{item.title}}</div>
                    </a>
                </li>
            </ul>
		</div>
        <div class="mui-card" style="margin-top:10px">
            <ul class="mui-table-view mui-grid-view mui-grid-7">
                <li class="mui-table-view-cell mui-media mui-col-xs-4 mui-col-sm-4" v-for="(item,i) of list2" :key="i">
                    <a style="width:130px;height:70px;" href="#">
                        <img :src="item.img_url" style="width:20px;height:21px;">
                        <div class="mui-media-body">{{item.title}}</div>
                    </a>
                </li>
            </ul>
        </div>
        <div class="mui-card" style="margin-top:10px">
            <ul class="mui-table-view mui-grid-view mui-grid-7">
                <li class="mui-table-view-cell mui-media mui-col-xs-4 mui-col-sm-4" v-for="(item,i) of list3" :key="i">
                    <a style="width:130px;height:70px;" href="#">
                        <img :src="item.img_url" style="width:20px;height:21px;">
                        <div class="mui-media-body">{{item.title}}</div>
                    </a>
                </li>
            </ul>
        </div>
        <nav class="mui-bar mui-bar-tab">
			<a class="mui-tab-item mui-active" href="#"  @click="shouye">
				<span class="mui-icon mui-icon-home"></span>
				<span class="mui-tab-label">首页</span>
			</a>
			<a class="mui-tab-item" href="#" @click="fenlei">
				<span class="mui-icon mui-icon-bars"></span>
				<span class="mui-tab-label">分类</span>
			</a>
			<a class="mui-tab-item" href="#"  @click="cart">
				<span class="mui-icon mui-icon-extra mui-icon-extra-cart"></span>
				<span class="mui-tab-label">购物车</span>
			</a>
			<a class="mui-tab-item" href="#"  @click="my">
				<span class="mui-icon mui-icon-contact"></span>
				<span class="mui-tab-label">我的</span>
			</a>
	    </nav>   
    </div>
</template>
<script>
export default {
    data(){
        return {
            list1:[],
            list2:[],
            list3:[],
            u:"登录/注册",
        }
    },
    created(){
        //console.log(sessionStorage.getItem("uid"))
        this.handleMy1();
        this.handleMy2();
        this.handleMy3();
        if(sessionStorage.getItem("uname")){
            this.u=sessionStorage.getItem("uname");
        }
    },
    methods:{
        //获取用户的id和用户名 uname
        Login:function(){
            if(!sessionStorage.getItem("uname")){
                this.$router.push({path:'/Login'});
            }else{
                this.u=sessionStorage.getItem("uname");
            }
        },
        tuichu(){
            sessionStorage.setItem("uname",'')
            this.Login();
        },
        fenlei:function(){
            this.$router.push({path:'/Cation'});
        },
        cart:function(){
            this.$router.push({path:'/Shopcart'});
        },
        my:function(){
            this.$router.push({path:'/My'});
        },
        shouye:function(){
            this.$router.push({path:'/Home'});
        },
        back(){
            this.$router.go("-1");
        },
        handleMy1(){
            var url="http://127.0.0.1:3000/my1";
            this.axios.get(url).then(result=>{
                this.list1=result.data;
            })
        },
        handleMy2(){
            var url="http://127.0.0.1:3000/my2";
            this.axios.get(url).then(result=>{
                this.list2=result.data;
            })
        },
        handleMy3(){
            var url="http://127.0.0.1:3000/my3";
            this.axios.get(url).then(result=>{
                this.list3=result.data;
            })
        },
    }
}
</script>
<style>

    #heades{
       width: 100%;
       height: 50px;
       background: #fff;
       position: fixed;
       top:0px;
       z-index: 10;
    }
    #log{
        color: #000;
        font-size: 18px;
       position: relative;
       top: 17px;
       left: 127px;
    }
    .wel{
        width:100%;
        height: 200px;
        background: #ff8f76;
    }
    .wel span{
        position: absolute;
        top:90px;
        left: 130px;
        font-size: 14px;
        color: #fff;
    }
    .wel div{
        width: 130px;
        height: 30px;
        background: #fff;
        border-radius: 15px 15px;
        position: absolute;
        top:115px;
        left: 125px;
    }
    .wel div a{
        color: #000;
        font-size: 12px;
        line-height: 30px;
        margin-left: 40px;
    }
    .my span{
        font-size: 15px;
    }
    .my span:first-child{
        margin-top: 5px;
        margin-left: 5px;
    }
    .my span:last-child{
        margin-top: 5px;
        margin-left: 200px;
    }
    .tit{
        position: relative;
        top:10px;
        left: -10px;
    }
    #cations{
        margin-top: 14px;
    }
    #cation2{
        color: #000;
        margin-top: 17px;
    }
</style>



