<template>
    <div class="app-home">
        <header id="heade">
            <nav>
                <a id="cation" @click="search" class="mui-icon-bars mui-icon mui-icon-left-nav mui-pull-left"></a>
                <a href="">
                    <span  id="logo">花礼网</span>
                </a>
                
            </nav>
        </header>
        <!-- 轮播图 -->
        <mt-swipe>
            <mt-swipe-item class="mt-swipe" v-for="(item,i) of navlist" :key="i">
                <img :src="item.img_url">
            </mt-swipe-item>
        </mt-swipe>
        <!-- 列表 -->
        <nav class="procate">
            <div class="procate-item" v-for="(item,i) of iconlist" :key="i">
                <a href="">
                    <img :src="item.img_url">
                    <p>{{item.title}}</p>
                </a>
            </div>
        </nav>
        <div class="center">
            <img src="http://127.0.0.1:3000/img/center.gif"/>
        </div>
        <div class="xuan">
            <p>| 一秒选花</p>
        </div>
        <!-- 送友人 -->
        <!-- 第一列 -->
        <nav class="give">
            <div v-for="(item,i) of giveslist" :key="i">
                <a href="">
                    <span class="givelove">{{item.title}}</span>
                    <img :src="item.img_url">
                </a>
            </div>
        </nav>
        <!-- 送友人第二列 -->
        <nav class="giv">
            <div v-for="(item,i) of givslist" :key="i">
                <a href="">
                    <span class="givlove">{{item.title}}</span>
                    <img :src="item.img_url">
                </a>
            </div>
        </nav>
        <!-- 热销和特惠 -->
        <nav class="give">
            <div v-for="(item,i) of list" :key="i">
                <a href="">
                    <img :src="item.img_url">
                    <div id="font">
                        <span class="givelove">{{item.title}}</span>
                    </div>
                    
                </a>
            </div>
        </nav>
        <!-- 推荐 -->
        <div class="tuijian1">爱情鲜花推荐</div>
        <div id="rose" class="mui-card-header mui-card-media" v-for="(item,i) of RecommendList" :key="i">
            <img :src="item.img_url" @click.prevent="jumpInfo1" :data-rid="item.rid"/>
            <div class="mui-media-body">
                <h4>{{item.title}}</h4>
                <h5>{{item.subtitle}}</h5>
                <div class="jingx"><a href="#">{{item.yx}}</a></div>
                <div class="hot"><a href="#">{{item.bk}}</a></div>
                <div class="cart">
                    <h3 style="margin-left:31px">￥{{item.price}}</h3>
                    <p id="car" style="width:25px;height:25px"></p>
                </div>
                <span style="padding:18px">{{item.sale}}</span>
            </div>
        </div>
        <div class="more1"><a href="#" @click="lookmore">查看更多</a></div>
        <!-- 永生花推荐1 -->
        <div class="tuijian2">永生花推荐</div>
        <nav class="lmom">
            <div class="lmo" v-for="(item,i) of LmoList" :key="i">
                <a href="#">
                    <img style="width:175px;height:191px" @click.prevent="jumpInfo2" :data-lid="item.lid" :src="item.img_url"/>
                    <div class="lmo1">
                        <div class="jingxuan"><a href="#">{{item.bk}}</a></div>
                        <h4>{{item.title}}</h4>
                        <div class="cart">
                            <h3>￥{{item.price}}</h3>
                            <p id="car" style="width:25px;height:25px"></p>
                        </div>
                        <p style="padding:18px">{{item.sale}}</p>
                    </div>
                </a>
            </div>
        </nav>
        <!-- 永生花推荐2 -->
        <nav class="lmom">
            <div class="lmo" v-for="(item,i) of LmomList" :key="i">
                <a href="#">
                    <img style="width:175px;height:191px" @click.prevent="jumpInfo2" :data-lid="item.lid" :src="item.img_url"/>
                    <div class="lmo1">
                        <div class="jingxuan"><a href="#">{{item.bk}}</a></div>
                        <h4>{{item.title}}</h4>
                        <div class="cart">
                            <h3>￥{{item.price}}</h3>
                           <p id="car" style="width:25px;height:25px"></p>
                        </div>
                        <p style="padding:18px">{{item.sale}}</p>
                    </div>
                </a>
            </div>
        </nav>
        <div class="more1"><a href="#" @click="more">查看更多</a></div>
        <!-- 蛋糕推荐 1-->
        <div class="tuijian2">蛋糕推荐</div>
        <nav class="lmom">
            <div class="lmo" v-for="(item,i) of cakeList" :key="i">
                <a href="#">
                    <img style="width:175px;height:181px" @click.prevent="jumpInfo3" :data-cid="item.cid" :src="item.img_url"/>
                    <div class="lmo1">
                        <div class="jingxuan"><a href="#">{{item.bk}}</a></div>
                        <h4>{{item.title}}</h4>
                        <div class="cart">
                            <h3>￥{{item.price}}</h3>
                            <p id="car" style="width:25px;height:25px"></p>
                        </div>
                        <p  style="padding:18px">{{item.sale}}</p>
                    </div>
                </a>
            </div>
        </nav>
        <!-- 蛋糕推荐2 -->
        <nav class="lmom">
            <div class="lmo" v-for="(item,i) of cakesList" :key="i">
                <a href="#">
                    <img style="width:175px;height:181px" :src="item.img_url"/>
                    <div class="lmo1">
                        <div class="jingxuan"><a href="#">{{item.bk}}</a></div>
                        <h4>{{item.title}}</h4>
                        <div class="cart">
                            <h3>￥{{item.price}}</h3>
                            <p id="car" style="width:25px;height:25px"></p>
                        </div>
                        <p style="padding:18px">{{item.sale}}</p>
                    </div>
                </a>
            </div>
        </nav>
        <div class="more1"><a href="#" @click="lookMores">查看更多</a></div>
        <div><img style="width:100%;height:125px;margin-top:20px" src="http://127.0.0.1:3000/img/banner4.png"/></div>
        <div style="text-align:center;margin-top:20px">为什么选择我们</div>
        <!-- 选择我们 六宫格 -->
        <ul class="mui-table-view mui-grid-view mui-grid-6">
		    <li class="mui-table-view-cell mui-media mui-col-xs-4 mui-col-sm-3" v-for="(item,i) of usList" :key="i">
                <a style="width:138px;height:132px;" href="#">
		            <img :src="item.img_url">
		            <div class="mui-media-body">{{item.title}}</div>
                </a>
            </li>
        </ul>
        <!-- 标签 -->        
        <footer>
            <div id="label1">
                <a href="#" class="footer-links-item">企业团购</a>
                <a href="#" class="footer-links-item">设计师款</a>
                <a href="#" class="footer-links-item">同城鲜花</a>
                <a href="#" class="footer-links-item">花语大全</a>
            </div>
            <div id="label2">
                <a href="#" class="footer-links-item">花礼优势</a>
                <a href="#" class="footer-links-item">勇敢爱</a>
                <a href="#" class="footer-links-item">常见问题</a>
                <a href="#" class="footer-links-item">关于我们</a>
            </div>
            <div>
                <p id="footer">
                    Copyright © 2005~2019 花礼网（中国鲜花礼品网）版权所有<br>中国鲜花网领先品牌，鲜花速递专家！
                </p>
            </div>
        </footer>
        <nav class="mui-bar mui-bar-tab">
			<a class="mui-tab-item mui-active" href="#"  @click="shouye">
				<span class="mui-icon mui-icon-home"></span>
				<span class="mui-tab-label">首页</span>
			</a>
			<a class="mui-tab-item" href="#" @click="fenlei">
				<span class="mui-icon mui-icon-bars"></span>
				<span class="mui-tab-label">分类</span>
			</a>
			<a class="mui-tab-item" href="#"  @click="cart">
				<span class="mui-icon mui-icon-extra mui-icon-extra-cart"></span>
				<span class="mui-tab-label">购物车</span>
			</a>
			<a class="mui-tab-item" href="#"  @click="my">
				<span class="mui-icon mui-icon-contact"></span>
				<span class="mui-tab-label">我的</span>
			</a>
	    </nav>
    </div>
</template>
<script>
export default {
    data(){
        return {
            navlist:[],  //轮播图的列表
            iconlist:[], 
            giveslist:[],
            givslist:[],
            list:[],
            RecommendList:[],
            LmoList:[],
            LmomList:[],
            cakeList:[],
            cakesList:[],
            usList:[],
            
        }
    },
    created(){
        this.handleImage();
        this.handlelist();
        this.handleGives();
        this.handleGivs();
        this.handleList();
        this.handleRecommend();
        this.handleLmo();
        this.handleLmom();
        this.handleCake();
        this.handleCakes();
        this.handleUs();
    },
    methods:{
        jumpInfo1(e){
            var rid=e.target.dataset.rid;
            //console.log(lid);
            this.$router.push("/homeinfo?rid="+rid);
        },
        jumpInfo2(e){
            var lid=e.target.dataset.lid;
            //console.log(lid);
            this.$router.push("/homeinfo1?lid="+lid);
        },
        jumpInfo3(e){
            var cid=e.target.dataset.cid;
            //console.log(lid);
            this.$router.push("/homeinfo2?cid="+cid);
        },
        search:function(){
            this.$router.push({path:"/Cation"});
        },
        lookmore:function(){
            this.$router.push({path:"/Love"});
        },
        more:function(){
            this.$router.push({path:"/Life"});
        },
        lookMores:function(){
            this.$router.push({path:"/Cake"});
        },
        fenlei:function(){
            this.$router.push({path:'/Cation'});
        },
        cart:function(){
            this.$router.push({path:'/Shopcart'});
        },
        my:function(){
            this.$router.push({path:'/My'});
        },
        shouye:function(){
            this.$router.push({path:'/Home'});
        },
        handlelist(){
            //1.发送ajax获取轮播数据
            var url = "http://127.0.0.1:3000/iconlist";
            this.axios.get(url).then(result=>{
                //2.获取返回结果
                //console.log(result.data);
                //3.保存data属性中
                this.iconlist = result.data;
            });
        },
        //加载轮播图的数据
        handleImage(){
            //1.发送ajax获取轮播数据
            var url = "http://127.0.0.1:3000/imglist";
            this.axios.get(url).then(result=>{
                //2.获取返回结果
                //console.log(result.data.data);
                //3.保存data属性中
                this.navlist = result.data.data;
            })
        },
        handleGives(){
            var url="http://127.0.0.1:3000/givelist";
            this.axios.get(url).then(result=>{
                //console.log(result);
                this.giveslist=result.data;
                //console.log(this.giveslist);
            })
        },
        handleGivs(){
            var url="http://127.0.0.1:3000/givlist";
            this.axios.get(url).then(result=>{
                //console.log(result);
                this.givslist=result.data;
                //console.log(this.giveslist);
            })
        },
        handleList(){
            var url="http://127.0.0.1:3000/list";
            this.axios.get(url).then(result=>{
                this.list=result.data;         
            })
        },
        handleRecommend(){
            var url="http://127.0.0.1:3000/Recommend";
            this.axios.get(url).then(result=>{
                //console.log(result);
                this.RecommendList=result.data;
            })
        },
        handleLmo(){
            var url="http://127.0.0.1:3000/lmo";
            this.axios.get(url).then(result=>{
                //console.log(result);
                this.LmoList=result.data;
            })
        },
        handleLmom(){
            var url="http://127.0.0.1:3000/lmom";
            this.axios.get(url).then(result=>{
                //console.log(result);
                this.LmomList=result.data;
            })
        },
        handleCake(){
            var url="http://127.0.0.1:3000/cakelist";
            this.axios.get(url).then(result=>{
                //console.log(result);
                this.cakeList=result.data;
            })
        },
        handleCakes(){
            var url="http://127.0.0.1:3000/cakeslist";
            this.axios.get(url).then(result=>{
                //console.log(result);
                this.cakesList=result.data;
            })
        },
        handleUs(){
            var url="http://127.0.0.1:3000/uslist";
            this.axios.get(url).then(result=>{
                //console.log(result);
                this.usList=result.data;
            })
        }
    }
}
</script>
<style>
    .mt-swipe{
        position: relative;
        top:50px;
    }
    .app-home .mint-swipe{
        height: 200px;
    }
    .app-home .mint-swipe img{
        width:100%;
    }
    .mui-table-view a{
        width: 40px;
        height: 40px;
    }
    .procate{
        display: flex; 
    }
    .procate-item a img{
        width: 80px;
        height: 80px;
        padding: 20px;
        margin-left: -5px;
        margin-top: 10px;
    }
    .procate-item a p{
        margin-top: -15px;
        margin-left: 19px;
        color: #000;
    }
    .center img{
        width: 100%;
        height: 100px;
    }
    .xuan p{
        font-size: 16px;
        color: #000;
        margin-left: 20px;
        margin-top: 10px;
    }
    .give{
        display: flex;
        position: relative;
    }
    .give div a img{
        width: 117px;
        height: 130px;
    }
    .givelove{
        margin-left: 34px;
        color: #000;
        font-size: 16px;
    }
    .giv{
        display: flex;
        position: relative;
    }
    .giv div a img{
        width: 85px;
        height: 97px;
    }
    .givlove{
        margin-left: 10px;
        color: #000;
        font-size: 16px;
    }
    #font{
        width: 100%;
        height: 20px;
        background: #514d5e;
        position: relative;
        top: -25px;
    }
    .tuijian1{
        text-align: center;
    }
    #rose{
        position: relative;
        top: 20px;
    }
    #rose img{
        width: 180px;
        height: 185px;
    }
    #rose div h4,h5{
        font-size: 16px;
        color: #514d5e;
        position: relative;
        left: 10px;
    }
    .jingx{
        width: 80px;
        height: 25px;
        border: 1px solid #ff894c;
        border-radius: 15px 15px;
        margin-top: 20px;
        margin-left: 140px;
    }
    .jingx a{
        margin-left: 10px;
        font-size: 14px;
        color: orangered;
    }
    .hot{
        margin-top: 10px;
        width: 100%;
        height: 30px;
        border-top: 1px solid #ddd;
        border-bottom:1px solid #ddd; 
    }
    .hot a{
        color: gray;
        line-height: 30px;
        position: relative;
        left: 13px;
    }
    #rose h3{
        color: orangered;
    }
    .cart{
        display: flex;
    }
    #car{
        margin-left: 70px;
        margin-top: 5px;
    }
    .more1{
        width: 110px;
        height: 30px;
        border: 1px solid #000;
        margin-top: 30px;
        margin-left: 135px;
    }
    .more1 a{
        color: #000;
        font-size: 15px;
        margin-left: 20px;
    }
    .tuijian2{
        text-align: center;
        margin-top: 30px;
    }
    #lmo{
        width: 200px;
        height: 20px;
    }
    .jingxuan{
        width: 80px;
        height: 25px;
        border: 2px solid #ff894c;
        border-radius: 15px 15px;
    }
    .jingxuan a{
        margin-left: 10px;
        font-size: 14px;
        color: #ff894c;
    }
    .lmo1 h4{
        font-size: 15px;
        color: #71797f;
    }
    .cart h3{
        color: orangered;
        font-size: 18px;
    }
    .lmom{
        display: flex;
        padding: 15px 10px;
    }
    /* 六宫格图片宽度 */
    .app-home .mui-grid-6 img{
        width:60px;
        height: 60px;
    }
    #label1,#label2{
        display: flex;
    }
    .footer-links-item{
        display: block;
        flex: 1;
        padding: 0.28571429rem 0.57142857rem;
        background: #e9ecf0;
        font-size: 0.85714286rem;
        text-align: center;
        color: #000;
    }
    #footer{
       height: 100px;
        font-size: 10px;
        text-align: center;
    }
</style>



