<template>
    <div class="app-cation">
        <header id="head">
            <div id="pic" class="mui-icon mui-icon-back" @click="back"></div>
            <div id="pict" class="mui-icon mui-icon-search">
                <input type="text" placeholder="请输入关键词">
            </div>
        </header>
        <div id="fenlei">
            <p><a @click="show1" data-id=1>热门推荐</a></p>
            <p><a @click="show1" data-id=2>鲜花</a></p>
            <p><a @click="show1" data-id=3>永生花</a></p>
            <p><a @click="show1" data-id=4>蛋糕</a></p>
            <p><a @click="show1" data-id=5>礼篮</a></p>
            <p><a @click="show1" data-id=6>绿植花卉</a></p>
        </div>
        <!-- 热销推荐 -->
        <div :class="active==1?'show':'none'" id="all">
            <header id="banner">
                    <img src="http://127.0.0.1:3000/img/lm.png"/>
            </header>
            <ul id="lifes" class="mui-table-view mui-grid-view mui-grid-9">
                <li class="mui-table-view-cell mui-media mui-col-xs-4 mui-col-sm-3" v-for="(item,i) of hotList" :key="i">
                    <a href="#" style="width:80px;height:100px;">
                        <img :src="item.img_url"  style="width:60px;height:60px;">
                        <div class="mui-media-body">{{item.title}}</div>
                    </a>
                </li>
            </ul>
        </div>
        <!-- 鲜花推荐 -->
        <div :class="active==2?'show':'none'" id="all">
            <header id="banner">
                    <img src="http://127.0.0.1:3000/img1/banner1.jpg"/>
            </header>
            <ul id="lifes" class="mui-table-view mui-grid-view mui-grid-9">
                <li class="mui-table-view-cell mui-media mui-col-xs-4 mui-col-sm-3" v-for="(item,i) of rose1" :key="i">
                    <a href="#" style="width:80px;height:100px;">
                        <img :src="item.img_url"  style="width:60px;height:60px;">
                        <div class="mui-media-body">{{item.title}}</div>
                    </a>
                </li>
            </ul>
        </div> 
        <!-- 永生花 -->
        <div :class="active==3?'show':'none'" id="all">
            <header id="banner">
                    <img src="http://127.0.0.1:3000/img1/banner2.jpg"/>
            </header>
            <ul id="lifes" class="mui-table-view mui-grid-view mui-grid-9">
                <li class="mui-table-view-cell mui-media mui-col-xs-4 mui-col-sm-3" v-for="(item,i) of life1" :key="i">
                    <a href="#" style="width:84px;height:100px;">
                        <img :src="item.img_url"  style="width:60px;height:60px;">
                        <div class="mui-media-body">{{item.title}}</div>
                    </a>
                </li>
            </ul>
        </div>
        <!-- 蛋糕 -->
        <div :class="active==4?'show':'none'" id="all">
            <header id="banner">
                    <img src="http://127.0.0.1:3000/img2/banner3.jpg"/>
            </header>
            <ul id="lifes" class="mui-table-view mui-grid-view mui-grid-12">
                <h5>精选品牌蛋糕</h5>
                <li class="mui-table-view-cell mui-media mui-col-xs-4 mui-col-sm-3" v-for="(item,i) of cake1" :key="i">
                    <a href="#" style="width:88.46px;height:66px;">
                        <img :src="item.img_url"  style="width:80px;height:44px;">
                    </a>
                </li>
            </ul>
        </div>
        <!-- 礼篮 -->
        <div :class="active==5?'show':'none'" id="all">
            <header id="banner">
                    <img src="http://127.0.0.1:3000/img2/banner4.jpg"/>
            </header>
            <ul id="lifes" class="mui-table-view mui-grid-view mui-grid-3">
                <h5>礼篮</h5>
                <li class="mui-table-view-cell mui-media mui-col-xs-4 mui-col-sm-3" v-for="(item,i) of hamper1" :key="i">
                    <a href="#" style="width:84px;height:100px;">
                        <img :src="item.img_url"  style="width:60px;height:60px;">
                        <div class="mui-media-body">{{item.title}}</div>
                    </a>
                </li>
            </ul>
        </div>     
        <!-- 绿植花卉 -->
        <div :class="active==6?'show':'none'" id="all">
            <header id="banner">
                    <img src="http://127.0.0.1:3000/img2/banner5.jpg"/>
            </header>
            <ul id="lifes" class="mui-table-view mui-grid-view mui-grid-3">
                <h5>绿植花卉</h5>
                <li class="mui-table-view-cell mui-media mui-col-xs-4 mui-col-sm-3" v-for="(item,i) of plant1" :key="i">
                    <a href="#" style="width:84px;height:100px;">
                        <img :src="item.img_url"  style="width:60px;height:60px;">
                        <div class="mui-media-body">{{item.title}}</div>
                    </a>
                </li>
            </ul>
        </div>            
        <nav class="mui-bar mui-bar-tab">
			<a class="mui-tab-item mui-active" href="#"  @click="shouye">
				<span class="mui-icon mui-icon-home"></span>
				<span class="mui-tab-label">首页</span>
			</a>
			<a class="mui-tab-item" href="#" @click="fenlei">
				<span class="mui-icon mui-icon-bars"></span>
				<span class="mui-tab-label">分类</span>
			</a>
			<a class="mui-tab-item" href="#"  @click="cart">
				<span class="mui-icon mui-icon-extra mui-icon-extra-cart"></span>
				<span class="mui-tab-label">购物车</span>
			</a>
			<a class="mui-tab-item" href="#"  @click="my">
				<span class="mui-icon mui-icon-contact"></span>
				<span class="mui-tab-label">我的</span>
			</a>
	    </nav>
    </div> 
</template>
<script>
export default {
    data(){
        return {
            active:1,
            hotList:[], 
            rose1:[],
            life1:[],
            cake1:[],
            hamper1:[],
            plant1:[],
            list:[{title:""}],
        }
    },
    
    created(){
        this.handleHot();
        this.handleRose1();
        this.handlelife1();
        this.handlecake1();
        this.handlehamper1();
        this.handleplant1();
    },
    methods:{
        submit:function(e){
            var key=e.details.key;
            console.log(key);
            var url="http://127.0.0.1:3000/search";
            this.axios.get(url).then(result=>{
                    
                    console.log(result);
                })
        },
        //返回上一页
        back(){
            this.$router.go("-1");
        },
        fenlei:function(){
            this.$router.push({path:'/Cation'});
        },
        cart:function(){
            this.$router.push({path:'/Shopcart'});
        },
        my:function(){
            this.$router.push({path:'/My'});
        },
        shouye:function(){
            this.$router.push({path:'/Home'});
        },
        show1(e){
            this.active=e.target.dataset.id;
        },
        handleHot(){
            var url="http://127.0.0.1:3000/hot";
            this.axios.get(url).then(result=>{
                //console.log(result);
                this.hotList=result.data;
            })
        },
        handleRose1(){
            var url="http://127.0.0.1:3000/rose1";
            this.axios.get(url).then(result=>{
                this.rose1=result.data;
            })
        },
        handlelife1(){
            var url="http://127.0.0.1:3000/life1";
            this.axios.get(url).then(result=>{
                this.life1=result.data;
            })
        },
        handlecake1(){
            var url="http://127.0.0.1:3000/cake1";
            this.axios.get(url).then(result=>{
                this.cake1=result.data;
            })
        },
        handlehamper1(){
            var url="http://127.0.0.1:3000/hamper1";
            this.axios.get(url).then(result=>{
                this.hamper1=result.data;
            })
        },
        handleplant1(){
            var url="http://127.0.0.1:3000/plant1";
            this.axios.get(url).then(result=>{
                this.plant1=result.data;
            })
        },
    }
}
</script>
<style>
    #lifes{
        width: 268px;
        height: 0px;
        position: relative;
        top:225px;
        left: 100px;
        background: #efeff4;
        border: 0;
    }
    .none{
        display: none;
    }
    .show{
        display: block;
    }
    #fenlei{
        position:absolute;
        top:80px;
        text-align: center;
    }
    #fenlei p a{
        color: #000;
        line-height: 55px;
        font-size: 16px;
    }
    #fenlei p a:hover{
        color: #ff894c;
    }
    #all{
       position:absolute;
        top:-45px; 
    }
    #head input{
        width: 300px;
        height: 33px;
        margin-left: 0px;
        background: #e9ecf0;
        border: 0;
        margin-top: 3px;
    } 
    #head{
        display: flex;
        margin-top: 10px;
        background: #fff;
        width: 100%;
        height: 38px;
    }
    #pic{
        color: gray;
        width: 50px;
        height: 50px;
        margin-top: 10px;
    }
    #hot{
        width: 100px;
        height: 40px;
        margin-top: 15px;
        text-align: center;
    }
    #hot a{
        color: #000;
        font-size: 15px;
    }
    #banner img{
        width: 260px;
        height: 100px;
        position: absolute;
        top:108px;
        left: 103px;
    }
   #list{
       margin-left: 150px;
   }
   #lists a{
       width: 100px;
       height: 100%;
   }
   #pict{
       margin-left: 20px;
   }
   #row1{
       display: flex;
       position: absolute;
       top: 210px;
       left: 65px;
   }
   #row1 li a img{
       width: 70px;
       height: 70px;
       margin: 10px;
   }
   #row2{
       display: flex;
       position: absolute;
       top: 320px;
       left: 65px;
   }
   #row2 li a img{
       width: 70px;
       height: 70px;
       margin: 10px;
   }
   #row3{
       display: flex;
       position: absolute;
       top: 430px;
       left: 65px;
   }
   #row3 li a img{
       width: 70px;
       height: 70px;
       margin: 10px;
   }
  
    /* 九宫格图片宽度 */
    .app-home .mui-grid-9 img{
        width:160px;
        height: 160px;
    }
</style>



