<template>
    <div class="app-goodlist">
        <header id="heade">
            <nav>
                <a id="cation" @click="back" class="mui-icon mui-icon-back mui-icon-left-nav mui-pull-left"></a>
                <a href="">
                    <span  id="logo">购物车</span>
                </a>
                <a href="" class="navs mui-icon mui-icon-bars mui-icon-right-nav mui-pull-right">
                    
                </a>
            </nav>
        </header>
        <!-- 商品详情信息 start -->
        <div class="goods-item" v-for="(item,i) of list" :key="i">
            <!-- GoodList.vue -->
                <img :src="item.img_url"  @click.prevent="jumpInfo" :data-lid="item.lid"/>
                <div class="lmo1">
                    <div class="jingxuan1"><a href="#">{{item.bk}}</a></div>
                    <h4>{{item.title}}</h4>
                    <div class="cart">
                        <h3>￥{{item.price}}</h3>
                    </div>
                </div>
        </div>
        <!-- 商品详细信息 end -->
        <!-- 加载更多 -->
        <mt-button size="large" type="primary" style="background:#ccc" @click="looksMore">加载更多</mt-button>
    </div>
</template>
<script>
export default {
    data(){
        return {
            list:[],   //模板中显示的商品列表
            pno:0   //第几页
        }
    },
    created(){
        //当组件创建成功后立即调用加载下一页方法
        this.looksMore();
    },
    methods:{
        back(){
            this.$router.go("-1");
        },
        //跳转到商品详情组件
        jumpInfo(e){
            var lid=e.target.dataset.lid;
            this.$router.push("/Cakeinfo?lid="+lid);
        },
        //加载下一页数据并且将数据显示在模板上
        looksMore(){
            //0.获取当前页码
            this.pno++;
            //console.log(123);
            //1.创建一个变量url请求地址
            var url = "http://127.0.0.1:3000/cake?pno="+this.pno;
            //2.发送ajax请求
            this.axios.get(url).then(result=>{
                var rows = this.list.concat(result.data.data);
                this.list = rows;
                //console.log(this.list);
            })
        }
    },
}
</script>
<style>
/* 外层父元素 */
.app-goodlist{
    display: flex;
    flex-wrap: wrap;/*子元素换行*/
    justify-content: space-between;/*两端对齐*/
    padding: 4px;
}
/* 商品信息 */
.app-goodlist .goods-item{
    width:49%;
    border: 1px solid #ccc;
    margin: 2px 0;
    padding: 2px;
    display: flex;
    flex-direction: column;
    min-height: 245px;
    position: relative;
    top:40px;
}
/* 商品图片 */
.app-goodlist .goods-item img{
    width: 100%;
}
.jingxuan1{
        width: 80px;
        height: 25px;
        border: 1px solid #ff894c;
        border-radius: 15px 15px;
        margin-top: 15px;
        margin-left: 7px;
}
.jingxuan1 a{
    color: #ff894c;
}
</style>



