<template>
    <div class="app-Lifeinfo">
        <header id="heade">
            <nav>
                <a id="cation" @click="back" class="mui-icon mui-icon-back mui-icon-left-nav mui-pull-left"></a>
                <a href="">
                    <span  id="logo">购物车</span>
                </a>
                <a href="" class="navs mui-icon mui-icon-bars mui-icon-right-nav mui-pull-right">
                    
                </a>
            </nav>
        </header>
        <!-- 商品介绍 -->
        <div class="mui-card">
            <div class="lmo1">
                <img style="width:100%;height:309px" :src="info.img_url"/>
                <h4>{{info.title}}</h4>
                <div class="jingxuan"><a href="#">{{info.bk}}</a></div>
                <div class="cart">
                    <h3>￥{{info.price}}</h3>
                    <h5>{{info.sale}}</h5>
                </div>
            </div>
		</div>
        <!-- 花语 -->
        <div class="mui-card">
            <div>
                <dt>材料:</dt>
                <dd>{{info.cailiao}}</dd>   
            </div>
            <div>
                <dt>配送:</dt>
                <dd>{{info.save}}</dd>   
            </div>
            <div>
                <dt>附送:</dt>
                <dd>{{info.song}}</dd>
            </div>
        </div>
        <div class="mui-card">
            配送至<p class="mui-icon mui-icon-location"></p>
        </div>
        <div class="mui-card">
            <h4>订单评价</h4>
            <p></p>
        </div>
        <div class="mui-card">
			<div class="mui-card-content">
				<div class="mui-card-content-inner">
					<textarea  placeholder="你想说点什么呢" maxlength="120" v-model="msg"></textarea>
                    <mt-button style="background:#ff894c;" size="small" type="primary" @click="addComment">发表评论</mt-button>
				</div>
			</div>
			<div class="mui-card-footer"></div>
		</div> 
      <!--发表评论 end--> 
      <!--评论列表 start--> 
      		<div class="mui-card">
				<div class="mui-card-content">
					<div class="mui-card-content-inner">
						<div class="cmt-list">
                            <div class="cmt-item" v-for="(item,i) of list" :key="i">
                                <div> 第{{i+1}}楼 发表时间: {{item.ctime | datetimeFilter}}</div>
                                <div>评论内容:{{item.content}}</div>
                            </div>
                        </div>
					</div>
				</div>
			</div>
            <div class="mui-card">
                <footer>
                <div id="label1">
                <a href="#" class="footer-links-item">企业团购</a>
                <a href="#" class="footer-links-item">设计师款</a>
                <a href="#" class="footer-links-item">同城鲜花</a>
                <a href="#" class="footer-links-item">花语大全</a>
            </div>
            <div id="label2">
                <a href="#" class="footer-links-item">花礼优势</a>
                <a href="#" class="footer-links-item">勇敢爱</a>
                <a href="#" class="footer-links-item">常见问题</a>
                <a href="#" class="footer-links-item">关于我们</a>
            </div>
            <div>
                <p id="footer">
                    Copyright © 2005~2019 花礼网（中国鲜花礼品网）版权所有<br>中国鲜花网领先品牌，鲜花速递专家！
                </p>
            </div>
        </footer>
            </div>
            <nav class="mui-bar mui-bar-tab">
                    <a class="mui-tab-item mui-active" href="#tabbar">
                        <span class="mui-icon-extra mui-icon-extra-custom"></span>
                        <span class="mui-tab-label">客服</span>
                    </a>
                    <a class="mui-tab-item" href="#tabbar-with-chat">
                        <span class="mui-icon-extra mui-icon-extra-cart"></span>
                        <span class="mui-tab-label">购物车</span>
                    </a>
                    <a style="background:#435448;" class="mui-tab-item">
                        <span class="mui-tab-label buy" @click="addcart">加入购物车</span>
                    </a>
                    <a style="background:#ff894c" class="mui-tab-item">
                        <span class="mui-tab-label buy">立即购买</span>
                    </a>
            </nav>
    </div>    
</template>
<script>
import {Toast} from "mint-ui"
  export default {
    data(){
      return {
        list:[],     //评论内容
        pno:0,       //页码
        pageSize:5,  //页大小
        msg:"",
        lid:this.$route.query.lid,
        info:{},
      }
    },
    created(){
      this.loadMore();
      this.findProducts();
      //console.log(this.lid)    
    },
    methods:{
        back(){
            this.$router.go("-1");
        },
        //添加购物车功能
        addcart(){
            var pid=this.lid;
            var title=this.info.title;
            var img=this.info.img_url;
            var price=this.info.price;
            var count=this.info.count;
            var url="http://127.0.0.1:3000/addcart?pid="+pid+"&img_url="+img+"&title="+title+"&price="+price+"&count="+count;
            this.axios.get(url).then(result=>{
               console.log(result.data);
               this.$store.commit("increment");
            })
        },
        // 获取详细信息
        findProducts(){
            var url="http://127.0.0.1:3000/homeinfo1?lid="+this.lid;
            this.axios.get(url).then(result=>{
                //console.log(result)
                this.info=result.data[0];
            })
        },
      loadMore(){
        this.pno++;
        var ps = this.pageSize;
        var reg = /^[0-9]{1,}$/;
        if(!reg.test(this.pno)){
          Toast("页码格式不正确");
          return;
        }
        var url = "http://127.0.0.1:3000";
        url+="/getComment?nid="+this.nid;
        url+="&pno="+this.pno;
        this.axios.get(url).then(result=>{
          this.list = result.data.data;
        })
      },
      //添加评论
      addComment(){
        var nid = this.nid;
        var con = this.msg;
        var size = con.trim().length;
        if(size==0){
          Toast("评论内容不能为空");
          return;
        }
        var postData = this.qs.stringify({
              nid:nid,
              content:con
        });
        var url = "http://127.0.0.1:3000";
        url+="/addcomment";
        this.axios.post(url,postData).then(result=>{
            Toast("添加成功");
            this.msg = "";
            this.pno=0;//清空页码
            this.list=[];//清空原有数据
            this.loadMore();//第一页
        })    
      },

    }
  }
</script>
<style>
.cart{
    float: left;
}
.cart h5{
    margin-left: 190px;
}
.mui-card{
    position: relative;
    top:30px;
}
.mui-card div dd{
    color: gray;  
}
.buy{
    color: white;
}
.mui-tab-label{
    font-size: 14px;
}
 .footer-links-item{
        display: block;
        flex: 1;
        padding: 0.28571429rem 0.57142857rem;
        background: #e9ecf0;
        font-size: 0.85714286rem;
        text-align: center;
        color: #000;
    }
    #footer{
       height: 100px;
        font-size: 10px;
        text-align: center;
    }
</style>



