<template>
   <div id="all1">
     <header id="heade">
            <nav>
                <a id="cation" @click="search" class="mui-icon-bars mui-icon mui-icon-left-nav mui-pull-left"></a>
                <a href="">
                    <span  id="logo">花礼网</span>
                </a>
            </nav>
        </header>
        <form  @submit.prevent="login">
            <div id="forminput1">
              <input type="text" autocomplete="off" name="uname" v-model="uname" placeholder="请输入您的用户名"/>
            </div>
            <br/>
            <div id="forminput2">
              <input type="password" autocomplete="off" name="upwd" v-model="upwd" placeholder="请输入您的密码"/>
            </div>
            <br/>
            <div id="btn">
              <input type="button" value="登录" @click="btnLogin()"/>
            </div>  
        </form>   
        <div id="reg">
          <a href="#">手机验证></a>
          <a href="#">注册></a>
        </div>
        <div id="other">
          <img src="http://127.0.0.1:3000/img/qq.png"/>
          <img src="http://127.0.0.1:3000/img/zhi.png"/>
        </div>
        <nav class="mui-bar mui-bar-tab">
			<a class="mui-tab-item mui-active" href="#"  @click="shouye">
				<span class="mui-icon mui-icon-home"></span>
				<span class="mui-tab-label">首页</span>
			</a>
			<a class="mui-tab-item" href="#" @click="fenlei">
				<span class="mui-icon mui-icon-bars"></span>
				<span class="mui-tab-label">分类</span>
			</a>
			<a class="mui-tab-item" href="#"  @click="cart">
				<span class="mui-icon mui-icon-extra mui-icon-extra-cart"></span>
				<span class="mui-tab-label">购物车</span>
			</a>
			<a class="mui-tab-item" href="#"  @click="my">
				<span class="mui-icon mui-icon-contact"></span>
				<span class="mui-tab-label">我的</span>
			</a>
	    </nav>
   </div>
</template>
<script>
  //1.引入mint-ui Toast 局部使用
  import {Toast} from "mint-ui"
  export default {
    data(){
      return {
        uname:'',
        upwd:''
      }
    },
    methods:{
      search:function(){
            this.$router.push({path:"/Cation"});
        },
      fenlei:function(){
            this.$router.push({path:'/Cation'});
        },
        cart:function(){
            this.$router.push({path:'/Shopcart'});
        },
        my:function(){
            this.$router.push({path:'/Login'});
        },
        shouye:function(){
            this.$router.push({path:'/Home'});
        },
        show1(e){
            this.active=e.target.dataset.id;
        },
      btnLogin(){
        var u = this.uname;
        var p = this.upwd;
        //2.创建正则表达式验证用户和密码格式是否正确
        var reg = /^[a-z0-9_]{3,8}$/i;
        if(!reg.test(u)){
          Toast("用户名格式不正确,请检查");
          return;
        }
        if(!reg.test(p)){
          Toast("密码格式不正确,请检查");
          return;
        }
        var url = "http://127.0.0.1:3000/login?uname="+u+"&upwd="+p;
        this.axios.get(url).then(result=>{
           if(result.data.code==1){
             sessionStorage.setItem("uname",result.data.data[0].uname),
             //console.log(sessionStorage.getItem("uname"));
             //console.log(result.data);
             Toast("登录成功");
             this.$router.push({path: '/My'});
          }else{
            Toast("用户名或密码有误");
          }
        });

      }
    }
  }
</script>
<style>
#all{
  position: relative;
  top:0px;
}
#all1{
  position: relative;
  top:53px;
}
#forminput1{
    margin-top: 60px;
}
#forminput1>input{
    width:100%;
    height: 40px;
    border-radius:50px;   
}
.pwd{
    margin-top: 20px;
}
#forminput2>input{
    width:100%;
    height: 40px;
    border-radius:50px;   
}
#btn input{
    width: 100%;
    height: 40px;
    margin-top: 20px;
    color: aliceblue;
    font-size: 18px;
    background: tomato;
    border-radius: 50px;
}
#reg{
  margin-top: 20px;
  justify-content:flex-end;
}
#reg>a{
  color: gray;
}
#reg a:last-child{
  margin-left: 240px;
}
#other>img{
  width: 20px;
  height: 20px;
  margin-top: 60px;
}
#other img:first-child{
  margin-left: 100px;
}
#other img:last-child{
  margin-left: 120px;
}
</style>


