import Vue from 'vue'
import Router from 'vue-router'
// import HelloContainer from "./components/HelloWorld.vue"
//引入首页组件
import App from './App.vue'
import Home from "./components/tabbar/Home.vue"
import Login from "./components/tabbar/Login.vue"
import Reg from "./components/tabbar/Reg.vue"
import Cation from "./components/home/Cation.vue"
import Love from "./components/home/Love.vue"
import Loveinfo from "./components/home/Loveinfo.vue"
import Shopcart from "./components/home/Shopcart.vue"
import Life from "./components/home/Life.vue"
import Lifeinfo from "./components/home/Lifeinfo.vue"
import Cake from "./components/home/Cake.vue"
import Cakeinfo from "./components/home/Cakeinfo.vue"
import Homeinfo from "./components/home/Homeinfo.vue"
import Homeinfo1 from "./components/home/Homeinfo1.vue"
import Homeinfo2 from "./components/home/Homeinfo2.vue"
import My from "./components/home/My.vue"
Vue.use(Router)
//为shouye组件配置访问路径 /shouye
export default new Router({
  routes: [
    {path:'/Home',component:Home},
    {path:'/Login',component:Login},
    {path:'/Reg',component:Reg},
    {path:'/Cation',component:Cation},
    {path:'/Love',component:Love},
    {path:'/Loveinfo',component:Loveinfo},
    {path:'/Shopcart',component:Shopcart},
    {path:'/Life',component:Life},
    {path:'/Lifeinfo',component:Lifeinfo},
    {path:'/Cake',component:Cake},
    {path:'/Cakeinfo',component:Cakeinfo},
    {path:'/Homeinfo',component:Homeinfo},
    {path:'/Homeinfo1',component:Homeinfo1},
    {path:'/Homeinfo2',component:Homeinfo2},
    {path:'/My',component:My},
  ]
})


